import { onBeforeMount, onBeforeUnmount } from 'vue';
/**
 * 关闭页签
 * @param close
 * @param next
 */
export function closePage(close, next) {
  window.dispatchEvent(new CustomEvent('page:close', {
    detail: {
      close: close,
      next: next
    }
  }));
}
/**
 * 刷新页签
 * @param route
 */
export function refreshPage(route) {
  window.dispatchEvent(new CustomEvent('page:refresh', {
    detail: {
      route: route
    }
  }));
}
/**
 * 配置页签位置、标题等
 * @param route
 * @param options
 */
export function configPage(route, options) {
  window.dispatchEvent(new CustomEvent('page:config', {
    detail: {
      route: route,
      options: options
    }
  }));
}
export function onPageChange(listener) {
  onBeforeMount(function () {
    window.addEventListener('page:change', listener);
  });
  onBeforeUnmount(function () {
    window.addEventListener('page:change', listener);
  });
}